<template>
  <div class="representModelBox">
    <div class="represent-list"  v-for="(itemPresent,index) in representList" :key="index">
      <div class="represent-top">
        <h1>{{itemPresent[0].graduationYear}}届优秀就业代表</h1>
      </div>
      <div class="represent-body">
        <el-card v-for="(item,index) in itemPresent" :key="index">
          <img
            :src="item.picurl"
            class="image"
          />
          <div class="basic-info">
            <span>
              学号：
              <p>{{item.stuNo}}</p>
            </span>
            <span>
              姓名：
              <p>{{item.stuName}}</p>
            </span>
            <span>
              毕业年份：
              <p>{{item.graduationYear}}</p>
            </span>
            <span>
              就业岗位：
              <p :title="item.jobTitle">{{item.jobTitle}}</p>
            </span>
            <span>座右铭：</span>
            <span>{{item.motto}}</span>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>
<script>
import { interRepresent } from "../../../../../utils/employment.url";
export default {
  data() {
    return {
      representList: []
    };
  },
  methods: {
    //获取就业代表信息列表
    getRepresentList() {
      interRepresent({}, "get")
        .then(res => {
          if (res.status == 200) {
            // this.representList = res.data;
            this.representList = this.groupBy(res.data, function(item) {
              return [item.graduationYear]; //按照时间年月分组
            });
            // console.log(this.representList)
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //数组对象分组
    groupBy(array, f) {
      var groups = {};
      array.forEach(function(o) {
        var group = JSON.stringify(f(o));
        groups[group] = groups[group] || [];
        groups[group].push(o);
      });
      return Object.keys(groups).map(function(group) {
        return groups[group];
      });
    }
  },
  created() {
    this.getRepresentList();
  }
};
</script>
<style lang="scss" scoped>
.representModelBox {
  width: 1200px;
  margin: 10px auto;
  .represent-list {
    margin: 10px;
    .represent-top {
      border-bottom: 1px solid #333333;
      h1 {
        font-size: 16px;
        font-weight: 600;
        line-height: 40px;
      }
    }
    .represent-body {
      margin-top: 10px;
      .el-card {
        display: inline-block;
        width: 348px;
        margin: 10px 21px;
        ::v-deep .el-card__body {
          padding: 10px;
          img,
          .basic-info {
            display: inline-block;
            vertical-align: middle;
            height: 200px;
          }
          img {
            width: 150px;
            border-radius: 2px;
          }
          .basic-info {
            width: 166px;
            margin-left: 10px;
            span {
              display: inline-block;
              margin-top: 10px;
              color: #333333;
              p {
                display: inline;
                color: #7b7b7b;
              }
            }
            :nth-of-type(4) {
              p {
                overflow: hidden;
                display: inline-block;
                width: 80px;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
            }
            :last-child{
                // line-height: 20px;
            }
          }
        }
      }
    }
  }
}
</style>